.App {
  text-align: center;
  font-size: 2.5rem;
  color: #fff;
}
body{
  background-image: linear-gradient(to right, #000428, #004e92, #082c08);
  padding: 100px 0;
}

.inputSec{
  display: flex;
  justify-content: center;
  flex-direction: column;
  padding: 0 200px;
}
.inputSec>input{
  padding: 15px;
  color: #FFF;
  letter-spacing: 2px;
  font-size: 1rem;
  background: transparent;
  border: 2px solid #fff;
  border-radius: 8px;
  width: 40%;
  margin: auto;
  outline: none;
}
button{
  width: 180px;
  height: 40px;
  background-image: linear-gradient(to left, #800410, #5b212e);
  font-size: 16px;
  letter-spacing: 1.8px;
  color: #FFFFFF;
  margin-left: 10px;
  margin-right: 10px;
}